import {React, useRef,useEffect} from 'react'
import "../Style/contact.scss"
import FormInput from '../components/FormInput/FormInput'
import emailjs from '@emailjs/browser';

import AOS from "aos"
import 'aos/dist/aos.css'


const Contactus = () => {
  useEffect(()=>{
    AOS.init({duration:1000})
    window.scrollTo(0,0)
  },[]);

  const form = useRef();
  const sendEmail = (e) => {
    emailjs.sendForm('service_u6chh07', 'template_tiaxw49', form.current, 'a4VXRdYScRcI93-9W')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
  }

  return (
    <>
      <div className="Contact">
        <div className="contactbox">
            <div className="BOX1">
              <h1>We love to hear from you</h1>

              <form ref={form}  className='form_div' onSubmit={sendEmail}  >
                 <FormInput 
                  type="text" 
                  name='firstname' autoComplete='off' 
                  id='firstname' placeholder="FirstName:" />
                <FormInput 
                  type="text" 
                  name='lastname' 
                  autoComplete='off' 
                  id='lastname' placeholder="LastName:" />
                <FormInput 
                  type="email" 
                  name='email' 
                  autoComplete='off' 
                  id='email' 
                  placeholder="Your email:" />
                <FormInput 
                  type="text" 
                  name='number' 
                  autoComplete='off' 
                  id='number'
                   placeholder="Your phone:" />
                <FormInput 
                  type="textarea" 
                  rows="10" 
                  cols="50" 
                  name='subject'
                  autoComplete='off' 
                  id='subject' placeholder="Subject:" />
                <div style={{width:"100%",padding:"10px"}}>
                  <textarea name="message" id="message" cols="40" rows="5" placeholder='Message'></textarea>
                </div>
                <div style={{width:"100%",padding:"10px"}}>
                  <span>
                    <button style={{padding:"8px 20px",border:"none",color:"white",backgroundColor:"#eb5e34",borderRadius:"10px",fontFamily: 'Roboto',fontWeight:"600"}} 
                    type='submit'>Submit</button>
                  </span>
                </div>
              </form>
            </div>


            <div className="BOX2" >
              <h1 data-aos="zoom-in">Our Office Address</h1>
              <div className="BOX">
                <h2>Head Office</h2>
                <p>Sallahghari, Bhaktapur (Near BurgerHouse)</p>
              </div>
              <div className="BOX">
                <h2>Phone:</h2>
                <p>9843651620</p>
                <p>9864837989</p>
                <p>9840090620</p>
              </div>
              <div className="BOX">
                <h2>Email:</h2>
                <p>ands.consult@gmail.com</p>
              </div>
            </div>
        </div>

        <div className="mapbox">
        <iframe 
        title='map'
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3533.5170282663626!2d85.4096544!3d27.670410699999998!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x39eb1ba8cf31dd5b%3A0xd6d8c2ff03de0a8c!2sThe%20Burger%20House%20and%20Crunchy%20Fried%20Chicken!5e0!3m2!1sen!2snp!4v1673888139736!5m2!1sen!2snp" 
            width="100%" 
            height="350" 
            style={{border:0}}
            allowFullScreen="" 
            loading="lazy" 
            referrerPolicy="no-referrer-when-downgrade">
         </iframe>
        </div>
      </div>
      
      
    </>
  )
}

export default Contactus