import React from 'react'
import { Link } from 'react-router-dom';

const Box3 = (props) => {
  return (
   <>
    <div className="box1">
            <div className="text">
                <h3>{props.title}</h3>
                <p>{props.paragraph}  <button><Link to={props.link}>Read More</Link></button></p>
            </div>
        </div>
   </>
  )
}

export default Box3