import React from 'react'

const Box1 = (props) => {
  return (
   <>
    <div className="box1">    
            <div className="img">
                {props.logo}
            </div>
            <div className="text">
                <h3>{props.title}</h3>
                <p>{props.paragraph}</p>
                
            </div>
        </div>
   </>
  )
}

export default Box1